body{
  background-color: rgb(178, 206, 230);
}
.navbar{
  background-color: rgb(107, 208, 231);
}
.container{
  background-color: white;
  padding: 15px;
  border-radius: 20px;
}

.input-contain{
  width: 50%;
  margin: auto;
}



.input{
  width:100% !important;
  border:1px solid green;

}

.boldName{
  font-weight: bold;
}

.grid {
  position: relative;
  display: grid;
  /* grid-template-columns: repeat(2,minmax(150px,1fr)); */
  /* grid-template-rows: repeat(2, minmax(150px,1fr)); */
  grid-column-gap: 10px;
  grid-row-gap: 10px;
}

.temp{
  font-size: 30px;
  font-weight: 700;
  margin-left: 10px;
}

img{
  width: 10% !important;
}

.row{
  font-size: 20px;
  font-weight: 600;
}

.navlink{
  text-decoration: none;
  font-size: 20px;
  color: skyblue;
}

/* Light Theme */
.light-theme {
  background-color: #ffffff !important;
  color: #000000 !important;
}

/* Dark Theme */
.dark-theme {
  background-color: #333333 !important;
  color: #ffffff !important;
}




@media only screen and (max-width:520px) {
  .input-contain{
    width: 70%;
    margin: auto;
  }

  .row{
    font-size: 12px;
    /* font-weight: 600; */
  }

  
  img{
    width: 40% !important;
  }

  .grid {
    position: relative;
    display: grid;
    /* grid-template-columns: repeat(2,1fr); */
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}
  
}